import { Line, mixins } from "vue-chartjs";
const { reactiveProp } = mixins;

export default {
  name: "LineChart",
  extends: Line,
  mixins: [reactiveProp],
  props: ['chartData', 'chartOptions', 'options', 'stepSizeY', 'v'],
  props: {
    chartData: {},
    stepSizeY: 10,
    maxY: 120, 
    chartOptions: {}
     
  },
  methods: {
    fillData() {
      return {
        labels: [
          "January" + this.getRandomInt(),
          "February",
          "March",
          "April",
          "May",
          "June",
          "July",
          "August",
          "September",
          "October",
          "November",
          "December"
        ],
        datasets: [
          {
            label: "Data One",
            backgroundColor: "rgba(11, 71, 188, 0.3)",
            borderColor: "rgba(6, 116, 188, 0.50)",
            pointBackgroundColor: "rgba(171, 71, 188, 1)",
            data: [
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt()
            ]
          },
          {
            label: "Data 2",
            backgroundColor: "rgba(171, 71, 188, 0.3)",
            borderColor: "rgba(6, 116, 188, 0.50)",
            pointBackgroundColor: "rgba(171, 71, 188, 1)",
            data: [
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt(),
              this.getRandomInt()
            ]
          }
        ]
      };
    },
    getRandomInt() {
      return Math.floor(Math.random() * (50 - 5 + 1)) + 5;
    }
  },
  mounted() {
    console.log(this.chartOptions);
    this.renderChart(
      this.chartData, 
      this.chartOptions
    );
  }
};
</script>